function sliderWorkshop() {
    var swiper = new Swiper('.workshop .swiper', {
        slidesPerView: 3,
        spaceBetween: 47,
        navigation: {
            nextEl: ".workshop .swiper-button-next",
            prevEl: ".workshop .swiper-button-prev",
        },
        breakpoints: {
            320: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            767: {
                slidesPerView: 1.5,
            },
            992: {
                slidesPerView: 3,
                spaceBetween: 47,
            },
        }
    })
}

function sliderSeason() {
    var swiper = new Swiper('.season .swiper', {
        slidesPerView: 4,
        spaceBetween: 132,
        navigation: {
            nextEl: ".season .swiper-button-next",
            prevEl: ".season .swiper-button-prev",
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 20,
            },
            768: {
                slidesPerView: 4,
                spaceBetween: 50,

            },
            992: {
                spaceBetween: 100,

            },
            1300: {
                spaceBetween: 132,
            },
        }
    })
}

$(document).ready(function() {

    sliderWorkshop();
    sliderSeason();

    // Select
    $(".custom_select select").niceSelect()

})